"use client"

import { motion } from 'framer-motion'
import { Github, ExternalLink } from 'lucide-react'

export default function ProjectCard({ project }){
  return (
    <motion.article
      initial={{ opacity: 0, y: 8, scale: 0.995 }}
      animate={{ opacity: 1, y: 0, scale: 1 }}
      whileHover={{ y: -6, scale: 1.05 }}
      transition={{ type: 'spring', stiffness: 240, damping: 20 }}
      className="card overflow-hidden"
    >
      {project.imageUrl && (
        <div className="w-full h-40 sm:h-44 bg-gradient-to-br from-neutral-800 via-neutral-900 to-neutral-800 overflow-hidden rounded-md">
          <img src={project.imageUrl} alt={`${project.title} cover`} className="w-full h-full object-cover opacity-95" />
        </div>
      )}

      <div className="mt-4 flex items-start justify-between">
        <div>
          <h3 className="text-lg font-semibold text-slate-50">{project.title}</h3>
          {project.description && <p className="mt-2 text-sm text-neutral-400">{project.description}</p>}
        </div>

        <div className="flex items-center gap-3">
          {project.githubLink && (
            <a href={project.githubLink} className="text-neutral-400 hover:text-neutral-200" aria-label="Repository" target="_blank" rel="noreferrer">
              <Github />
            </a>
          )}
          {project.liveLink && (
            <a href={project.liveLink} className="text-neutral-400 hover:text-neutral-200" aria-label="Live demo" target="_blank" rel="noreferrer">
              <ExternalLink />
            </a>
          )}
        </div>
      </div>

      <div className="mt-4 flex flex-wrap gap-2">
        {project.techStack?.map((t) => (
          <span key={t} className="text-xs px-2 py-1 rounded bg-neutral-800 text-neutral-300">{t}</span>
        ))}
      </div>
    </motion.article>
  )
}
